import router from "../router/";
import $config from "@/config/baseConfig";

/**
 * @description: 
 */
export const routeTo = (name, query = {}, params = {}) => {
  router.push({
    name: name,
    query: query,
    params: params
  });
};

/**
 * @description: 
 */
export const routeReplace = (name, query = {}, params = {}) => {
  router.replace({
    name: name,
    query: query,
    params: params
  });
};

/**
 * @description: URL
 */
export const getUrlParam = name => {
  let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //
  let r = window.location.search.substr(1).match(reg); //
  if (r != null) return decodeURI(r[2]);
  return null;
};

/**
 * @description: URL
 * @param {string} url url
 * @param {string} name 
 */
export const urlDeleteParam = (url, name) => {
  var urlArr = url.split("?");
  if (urlArr.length > 1 && urlArr[1].indexOf(name) > -1) {
    var query = urlArr[1];
    var obj = {};
    var arr = query.split("&");
    for (var i = 0; i < arr.length; i++) {
      arr[i] = arr[i].split("=");
      obj[arr[i][0]] = arr[i][1];
    }
    delete obj[name];
    var param = JSON.stringify(obj)
      // eslint-disable-next-line no-useless-escape
      .replace(/[\"\{\}]/g, "")
      // eslint-disable-next-line no-useless-escape
      .replace(/\:/g, "=")
      // eslint-disable-next-line no-useless-escape
      .replace(/\,/g, "&");
    var urlte = urlArr[0];
    if (param) urlte += "?" + param;

    return urlte;
  } else {
    return url;
  }
};

/**
 * @description: 
 * @param {type}
 * @return:
 */
export const findData = (data, keys) => {
  keys = keys.split(".");
  for (let i = 0; i < keys.length; i++) {
    if (!keys[i]) return data;
    data = data[keys[i]];
  }
  return data;
};

/**
 * @description: tree
 * @param {type}
 * @return:
 */
export const treeTransArray = (tree, key) => {
  return [].concat(
    ...tree.map(item => {
      if (item[key] && item[key].length) {
        return [].concat(item, ...treeTransArray(item[key]), key);
      } else {
        return item;
      }
    })
  );
};

/**
 * @description: 
 * @param {type}
 * @return:
 */
export const getAvatarUrl = url => {
  if (url) {
    return $config.fileUrl + url;
  } else {
    return "/images/avatar.jpg";
  }
};

export const hasClass = (elements, cName) => {
  return !!elements.className.match(new RegExp("(\\s|^)" + cName + "(\\s|$)"));
};

export const addClass = (elements, cName) => {
  if (!hasClass(elements, cName)) {
    elements.className += " " + cName;
  }
};

export const removeClass = (elements, cName) => {
  if (hasClass(elements, cName)) {
    elements.className = elements.className.replace(
      new RegExp("(\\s|^)" + cName + "(\\s|$)"),
      " "
    );
  }
};

export const checkPhone = phone => {
  if (!/^1(3|4|5|7|8)\d{9}$/.test(phone)) {
    return false;
  } else {
    return true;
  }
};

//
export const debounce = (func, ms = 1000) => {
  let timer;
  return function(...args) {
    if (timer) {
      clearTimeout(timer);
    }
    timer = setTimeout(() => {
      func.apply(this, args);
    }, ms);
  };
};

//
export const throttle = (func, ms = 1000) => {
  let canRun = true;
  return function(...args) {
    if (!canRun) return;
    canRun = false;
    setTimeout(() => {
      func.apply(this, args);
      canRun = true;
    }, ms);
  };
};
